




































































































import FilterSalary from '../components/filter/FilterCommission.vue'
import PopupFilter from '../components/filter/Filter.vue'
import PopupMixin from '@/mixins/popup'

import { Component, Mixins } from 'vue-property-decorator'
import SalaryRepository from '@/services/repositories/salary'
import { assign, forEach, trim } from 'lodash'
import { PaginationInterfaceSalary } from '@/interface'

import getRepository from '@/services'
import { namespace } from 'vuex-class'
const beAudit = namespace('beAudit')

@Component({
  components: { FilterSalary, PopupFilter }
})
export default class SalaryDVKD extends Mixins(PopupMixin) {
  @beAudit.Getter('getFilter') getFilter!: (rerport: string) => { contract: string; filters: Record<string, any> }
  salaryReq!: SalaryRepository
  data = []
  isLoading = false
  table = {
    total: 100,
    page: 1,
    limit: 20
  }
  query: PaginationInterfaceSalary = {
    total: 100,
    page: 1,
    limit: 20,
    contractStatus: null,
    keywordString: '',
    orderByClause: 1
  }
  param: Record<string, any> = {
    month: 0,
    year: 0,
    pageIndex: 1,
    pageSize: 20,
    orderByClause: 1
  }

  summary: Record<string, any> = {}
  isExcelLoading = false
  isPdfLoading = false

  mounted(): void {
    this.init()
  }

  get getIndex(): number {
    return this.table.limit * (this.table.page - 1) + 1
  }

  created(): void {
    this.salaryReq = getRepository('salary', {
      districtId: this.districtId
    })
    const today = new Date()
    this.param.year = today.getFullYear()
    this.param.month = today.getMonth() + 1
  }

  search(param: Record<string, any>): void {
    this.param = {
      ...this.param,
      ...param
    }
    this.param.pageIndex = 1
    this.table.page = 1

    this.init()
  }
  handleSort(command: number): void {
    console.log(command)
    this.param.orderByClause = command

    this.init()
  }
  init(): void {
    this.isLoading = true
    //@ts-ignore
    const filters = this.getFilter(this.$route.name)
    if (filters) {
      delete filters.filters.pageIndex
      delete filters.filters.pageSize
      this.param = assign(this.param, filters.filters)
    }
    const temp = {
      ...this.param,
      // orderByClause: 1,
      total: null
    }

    this.salaryReq
      .getSalariesOffice(temp)
      .then(res => {
        this.data = res.data
        this.isLoading = false
      })
      .catch(() => {
        this.isLoading = false
      })
    this.salaryReq.getSummaryOffice(temp).then(res => {
      this.table.total = res.data.total
      this.summary = res.data
    })
  }

  handleSearch(value: string): void {
    this.param.search = trim(value)
    this.param.pageIndex = 1
    this.table.page = 1

    this.init()
  }

  summaryMethod(params: { columns: any; data: Array<Record<string, any>> }): any[] {
    const { columns } = params
    const sums: any[] = []
    forEach(columns, (column, index: number) => {
      if (index < 2) {
        sums[index] = ''
        return
      }
      if (index === 2) {
        sums[index] = 'Tổng'
        return
      }
      const a = this.summary[column.property]

      //@ts-ignore
      sums[index] = a ? this.$options.filters.formatNumber(a) : ''
      if (index === 11) {
        const column = this.summary['resultMonthRevenueTotalPercent']
        //@ts-ignore
        sums[11] = this.$options.filters.formatPercentAdv(column)
      }
    })
    return sums
  }

  handleSizeChange(size: number): void {
    this.param.pageSize = size
    this.table.limit = size
    this.init()
  }
  handleCurrentChange(page: number): void {
    this.table.page = page
    this.param.pageIndex = page
    this.init()
  }
  handleOpenPopupFilter(): void {
    this.setOpenPopup({
      popupName: 'filter-salary',
      isOpen: true
    })
  }
  handleFilter(data: Record<string, any>): void {
    // const objectKeys = Object.keys(data)
    // forEach(objectKeys, elm => {
    //   if (data[elm]) {
    //     this.query[elm] = data[elm]
    //   } else {
    //     if (this.query[elm]) {
    //       delete this.query[elm]
    //     }
    //   }
    // })
    alert(123)
    this.param.pageIndex = 1
    this.table.page = 1

    this.init()
  }
  async handleExport(ext: string): Promise<void> {
    if (ext === 'xlsx') {
      this.isExcelLoading = true
    } else {
      this.isPdfLoading = true
    }
    if (!this.data.length) {
      this.$message.error('Không tìm thấy dữ liệu xuất file')
      this.isExcelLoading = false
      this.isPdfLoading = false
      return
    }
    this.salaryReq
      ?.exportReportOffice({
        ext: ext,
        ...this.param
      })
      .then(response => {
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        const date = new Date()
        const time = `${date.getFullYear()}${date.getMonth() + 1}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getMilliseconds()}`
        link.setAttribute('download', `Bao_cao_luong_DVKD_${time}.${ext === 'pdf' ? 'pdf' : 'xlsx'}`)
        document.body.appendChild(link)
        link.click()
        if (ext === 'xlsx') {
          this.isExcelLoading = false
        } else {
          this.isPdfLoading = false
        }
      })
      .catch(err => {
        if (ext === 'xlsx') {
          this.isExcelLoading = false
        } else {
          this.isPdfLoading = false
        }
      })
  }
}
